.app-view {
    width: 60%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: var(--text-color);
    overflow-y: scroll;
}

@media (max-width: 760px) {
    .app-view {
        overflow-y: visible;
        width: 100%;
        height: auto;
    }
}